export const undefinedPrice = null

export type TPrice = number | typeof undefinedPrice

export interface ICourse {
    id: string
    name: string
    prices: TPrice[]
}

export const filterByPrice = (courses: ICourse[], requiredRange: TPrice[]): ICourse[] => {
    const [startRange, endRange] = requiredRange

    //    ,     
    if (startRange === undefinedPrice && endRange === undefinedPrice) {
        return courses
    }

    //    "", ""
    if (startRange !== undefinedPrice && endRange !== undefinedPrice) {
        return courses.filter(course => {
            const [startPrice, endPrice] = course.prices

            //    ,      
            if (startPrice === undefinedPrice && endPrice === undefinedPrice) {
                return false
            }

            //      
            if (startPrice !== undefinedPrice && endPrice !== undefinedPrice) {
                return (startRange <= startPrice && startPrice <= endRange) //     
                    || (startRange <= endPrice && endPrice <= endRange) //     
                    || (startPrice <= startRange && endRange <= endPrice) //       
            }

            //     
            if (startPrice !== undefinedPrice && endPrice === undefinedPrice) {
                return startPrice <= endRange //      
            }

            //     
            if (startPrice === undefinedPrice && endPrice !== undefinedPrice) {
                return startRange <= endPrice //      
            }

            else return false
        })
    }

    //    ""
    if (startRange !== undefinedPrice && endRange === undefinedPrice) {
        return courses.filter(course => {
            const [startPrice, endPrice] = course.prices

            //    ,      
            if (startPrice === undefinedPrice && endPrice === undefinedPrice) {
                return false
            }

            //      
            if (startPrice !== undefinedPrice && endPrice !== undefinedPrice) {
                return startRange <= endPrice //      
            }

            //     
            if (startPrice !== undefinedPrice && endPrice === undefinedPrice) {
                return true
            }

            //     
            if (startPrice === undefinedPrice && endPrice !== undefinedPrice) {
                return startRange <= endPrice //      
            }

            else return false
        })
    }

    //    ""
    if (startRange === undefinedPrice && endRange !== undefinedPrice) {
        return courses.filter(course => {
            const [startPrice, endPrice] = course.prices

            //    ,      
            if (startPrice === undefinedPrice && endPrice === undefinedPrice) {
                return false
            }

            //      
            if (startPrice !== undefinedPrice && endPrice !== undefinedPrice) {
                return startPrice <= endRange //      
            }

            //     
            if (startPrice !== undefinedPrice && endPrice === undefinedPrice) {
                return startPrice <= endRange //      
            }

            //     
            if (startPrice === undefinedPrice && endPrice !== undefinedPrice) {
                return true
            }

            else return false
        })
    }

    else return courses
}